import "../../css/detail.css";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import ConfirmModal from "../confirmModal";
import { ADD_CART_REQUEST } from "../../reducers/cart";
const Detail = () => {
  const dispatch = useDispatch();
  const { products } = useSelector((state) => state.product);
  const { me } = useSelector((state) => state.user);
  const { addCartDone } = useSelector((state) => state.cart);
  const { id } = useParams();
  const navigate = useNavigate();
  const [selectedKind, setSelectedKind] = useState("");
  const [selectedOption, setSelectedOption] = useState("");
  const [sale, setSale] = useState(true);
  const [cnt, setCnt] = useState(1);
  const [openInfo1, setOpenInfo1] = useState(true);
  const [openInfo2, setOpenInfo2] = useState(true);
  const [openConfirmModal, setOpenConfirmModal] = useState(false);
  const [modalText, setModalText] = useState("");
  const handleKind = (e) => {
    setSelectedKind(e.target.value);
  };
  const handleOption = (e) => {
    setSelectedOption(e.target.value);
  };
  const handleCnt = (e) => {
    setCnt(e.target.value);
  };
  const product =
    products && products.find((product) => product.id === Number(id));
  const copyURL = () => {
    const url = window.location.href;
    navigator.clipboard
      .writeText(url)
      .then(() => {
        alert(" .");
      })
      .catch((error) => {
        console.error("URL  :", error);
      });
  };
  const handleClose = () => {
    setOpenConfirmModal(false);
  };
  const handleConfirm = () => {
    dispatch({
      type: ADD_CART_REQUEST,
      data: {
        user_id: me && Number(me.user_id),
        product_id: product.id,
        product_cnt: cnt,
      },
    });
  };
  useEffect(() => {
    if (addCartDone) {
      setOpenConfirmModal(false);
      // setModalText("  ");
      // setOpenConfirmModal(true);
    }
  }, [addCartDone]);
  if (product) {
    return (
      <div className="detail">
        <div className="nav_box">
          <p>HOME     3</p>
        </div>
        <div className="article_container">
          <div className="detail_container">
            <img src={product.product_mainImgSrc} alt="" />
            {[...Array(8)].map((_, index) => (
              <img
                key={index}
                src={`/details/detail_img${index + 1}.png`}
                alt=""
              />
            ))}
          </div>
          <div className="content_container">
            <div className="tag_box">
              <p>#</p>
              <p>#</p>
              <p>#</p>
            </div>
            <div className="name_box">
              <p className="name">{product.product_name}</p>
              <img src="/images/btn_share.png" alt="" onClick={copyURL} />
            </div>
            <div className="price_box">
              <p>{Number(product.product_truePrice).toLocaleString()}</p>
              <p>{Number(product.product_falsePrice).toLocaleString()}</p>
            </div>
            <div
              className="login_box"
              onClick={() => {
                navigate("/login");
              }}
            >
              <p>      .</p>
              <p>GO </p>
            </div>
            <div className="code_box">
              <div className="title_box">
                <p></p>
                <p></p>
                <p></p>
              </div>
              <div className="content_box">
                <p>d-0053</p>
                <p></p>
                <p>   </p>
              </div>
            </div>
            <p className="notice">
              <span>      .</span>
              <br />
                    .
              <br />
                ,     
               
              <br />
                .
            </p>
            <div className="sale_btn_box">
              <div
                className="sale_btn"
                onClick={() => {
                  setSale(true);
                }}
                style={{ borderColor: sale ? "#2b746a" : "rgb(219, 219, 219)" }}
              >
                <img src="/images/btn_check.png" alt="" />
                <p></p>
              </div>
              <div
                className="sale_btn"
                onClick={() => {
                  setSale(false);
                }}
                style={{ borderColor: sale ? "rgb(219, 219, 219)" : "#2b746a" }}
              >
                <img src="/images/btn_check.png" alt="" />
                <p></p>
              </div>
            </div>
            <div className="select_box">
              <p></p>
              <select value={selectedKind} onChange={handleKind}>
                <option value=""></option>
                <option value="kind1">1</option>
                <option value="kind2">2</option>
                <option value="kind3">3</option>
              </select>
            </div>
            <div className="option_box">
              <div className="option_text_box">
                <p></p>
                <div className="option_price_box">
                  <p>{Number(product.product_truePrice).toLocaleString()}</p>
                  <p>{Number(product.product_falsePrice).toLocaleString()}</p>
                </div>
              </div>
              <div className="option_select_box">
                <select value={selectedOption} onChange={handleOption}>
                  <option value=""> </option>
                  <option value="option1">1</option>
                  <option value="option2">2</option>
                  <option value="option3">3</option>
                </select>
              </div>
            </div>
            <div className="cnt_box">
              <p></p>
              <div className="cnt_input_box">
                <input type="number" value={cnt} onChange={handleCnt} />
                <p></p>
              </div>
            </div>
            <div className="total_box">
              <p>  </p>
              <p>
                {(Number(product.product_truePrice) * cnt).toLocaleString()}
              </p>
            </div>
            <div className="btn_box">
              <div
                className="btn"
                onClick={() => {
                  setModalText(" ?");
                  setOpenConfirmModal(true);
                }}
              >
                
              </div>
              <div
                className="btn"
                onClick={() => {
                  navigate("/order", { state: product });
                }}
              >
                
              </div>
            </div>
            <div className="info_box">
              <div
                className="info_title_box"
                onClick={() => {
                  setOpenInfo1(!openInfo1);
                }}
              >
                <p></p>
                <p>{openInfo1 ? "" : ""}</p>
              </div>
              {openInfo1 ? (
                <div className="info_content_box">
                  <p>
                     
                    <br />
                    1.  8~ 10(365 )
                    <br />
                    2.   24 ,  
                     
                    <br />
                    <br />
                       
                    <br />
                    1.  :  9~ 8
                    <br />
                    2. ,   :  9~ 7
                    <br />
                    <span>
                      (     , ,   
                            11 
                      . ,      ,
                             .)
                    </span>
                    <br />
                    <br />
                     
                    <br />
                    1.  
                    <br />
                    (, , ,   ,  
                           
                       .)
                    <br />
                    <br />
                     
                    <br />
                    1.    3  
                    <br />
                    2.  2~3  
                    <br />
                    3., ,     
                       .
                    <br />
                    (,       
                     )
                    <br />
                    4.        
                          .
                    <br />
                    5. , , , , ,
                         
                       ,     
                      .
                  </p>
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="info_box">
              <div
                className="info_title_box"
                onClick={() => {
                  setOpenInfo2(!openInfo2);
                }}
              >
                <p>/</p>
                <p>{openInfo2 ? "" : ""}</p>
              </div>
              {openInfo2 ? (
                <div className="info_content_box">
                  <p>
                    <span>
                      , ,     
                      <br />
                       1566-5565  .
                      <br />
                           48  100%
                      .
                    </span>
                    <br />
                    <br />
                        
                    <br />
                    1.         
                    <br />
                    2.     
                    <br />
                    3.     
                    <br />
                    <br />
                    * ,         
                           
                    .
                    <br />
                    <br />
                        
                    <br />
                    1.          
                           ,  
                     .
                    <br />
                    2.         
                        .
                    <br />
                    3.          
                        .
                    <br />
                    4.        
                        .
                    <br />
                    <br />
                        
                    <br />
                    1.     (  ,
                      ,   , 
                      ,   ) 
                     .
                    <br />
                    2. (,,,, )  
                      .
                    <br />
                    3.       
                          
                           .
                    <br />
                    4.        
                         .
                    <br />
                    5. (,)      
                    .
                    <br />
                    (      
                    )
                    <br /> 6.       2 ,
                       3  , ,  
                    .
                    <br />
                  </p>
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
        {openConfirmModal ? (
          <ConfirmModal
            data={modalText}
            onConfirm={handleConfirm}
            onClose={handleClose}
          />
        ) : (
          ""
        )}
      </div>
    );
  }
};

export default Detail;
