import "../../css/signin.css";
import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import useInput from "../hooks/useInput";
import { useDispatch, useSelector } from "react-redux";
import { SIGN_UP_REQUEST, CHECK_ID_REQUEST } from "../../reducers/user";
const Signin = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { signUpDone, signUpError, checkIdDone, checkIdError } = useSelector(
    (state) => state.user
  );
  const [userID, onChangeId] = useInput("");
  const [userPW, onChangePW] = useInput("");
  const [userName, onChangeUserName] = useInput("");
  const [userEmail, onChangeUserEmail] = useInput("");
  const [userTel, onChangeUserTel] = useInput("");
  const [checkListAll, setCheckListAll] = useState(false);
  const [checkList1, setCheckList1] = useState(false);
  const [checkList2, setCheckList2] = useState(false);
  const [checkList3, setCheckList3] = useState(false);
  const [checkList4, setCheckList4] = useState(false);
  const [openContent1, setOpenContent1] = useState(false);
  const [openContent2, setOpenContent2] = useState(false);
  const [checkIdComplete, setCheckIdComplete] = useState(false);
  const [passwordCheck, setPasswordCheck] = useState("");
  const [passwordError, setPasswordError] = useState(false);

  useEffect(() => {
    if (checkList1 && checkList2 && checkList3 && checkList4) {
      setCheckListAll(true);
    }
  }, [checkList1, checkList2, checkList3, checkList4]);

  useEffect(() => {
    if (checkIdError) {
      alert(checkIdError);
    }
  }, [checkIdError]);

  useEffect(() => {
    if (checkIdDone) {
      alert("   .");
      setCheckIdComplete(userID);
    }
  }, [checkIdDone]);

  useEffect(() => {
    if (signUpDone) {
      navigate("/");
    }
  }, [signUpDone]);

  useEffect(() => {
    if (signUpError) {
      alert(signUpError);
    }
  }, [signUpError]);

  const onChangePasswordCheck = (e) => {
    setPasswordCheck(e.target.value);
    setPasswordError(e.target.value !== userPW);
  };
  const checkId = (e) => {
    e.preventDefault();
    dispatch({
      type: CHECK_ID_REQUEST,
      data: {
        userID,
      },
    });
  };
  const onSubmitForm = (e) => {
    e.preventDefault();

    dispatch(
      {
        type: SIGN_UP_REQUEST,
        data: {
          userID,
          userPW,
          userName,
          userEmail,
          userTel,
          checkList1,
          checkList2,
          checkList3,
          checkList4,
        },
      },
      [
        userID,
        userPW,
        userName,
        userEmail,
        userTel,
        checkList1,
        checkList2,
        checkList3,
        checkList4,
      ]
    );
  };
  return (
    <div className="singin">
      <p className="title"> </p>
      <form className="input_box_container">
        <label className="input_box">
          <input
            type="text"
            name="userID"
            value={userID}
            onChange={onChangeId}
            placeholder=""
          />
          <div
            className="check_id"
            style={{
              backgroundColor: checkIdComplete ? "#2B746A" : "#b6b6b6",
            }}
            onClick={checkId}
          >
            
          </div>
        </label>
        <label className="input_box">
          <input
            type="password"
            name="userPW"
            value={userPW}
            onChange={onChangePW}
            placeholder=""
          />
        </label>
        <label
          className="input_box"
          style={{ marginBottom: passwordError ? "" : "1.6vw" }}
        >
          <input
            type="password"
            name="userPWCheck"
            value={passwordCheck}
            onChange={onChangePasswordCheck}
            placeholder=" "
          />
        </label>
        {passwordError && (
          <div style={{ color: "red", marginBottom: "1.6vw" }}>
              .
          </div>
        )}
        <label className="input_box">
          <input
            type="text"
            name="userName"
            value={userName}
            onChange={onChangeUserName}
            placeholder=""
          />
        </label>
        <label className="input_box">
          <input
            type="text"
            name="userEmail"
            value={userEmail}
            onChange={onChangeUserEmail}
            placeholder=""
          />
        </label>
        <label className="input_box">
          <input
            type="text"
            name="userTel"
            value={userTel}
            onChange={onChangeUserTel}
            placeholder=""
          />
        </label>
      </form>
      <div className="check_container">
        <div className="check_box">
          <img
            src={
              checkListAll ? "/images/box_check.jpg" : "/images/box_unCheck.jpg"
            }
            alt=""
            onClick={() => {
              if (!checkListAll) {
                setCheckList1(true);
                setCheckList2(true);
                setCheckList3(true);
                setCheckList4(true);
                setCheckListAll(true);
              } else {
                setCheckList1(false);
                setCheckList2(false);
                setCheckList3(false);
                setCheckList4(false);
                setCheckListAll(false);
              }
            }}
          />
          <p>   .</p>
        </div>
        <div className="check_box">
          <img
            src={
              checkList1 ? "/images/box_check.jpg" : "/images/box_unCheck.jpg"
            }
            alt=""
            onClick={() => {
              setCheckList1(!checkList1);
              if (checkList1) {
                setCheckListAll(false);
              }
            }}
          />
          <p>
             <span>()</span>
          </p>
          <div
            className="content_box"
            onClick={() => {
              setOpenContent1(!openContent1);
            }}
          >
            <p></p>
            <p>{openContent1 ? "" : ""}</p>
          </div>
        </div>
        {openContent1 ? (
          <div className="content">
            <p>
              () 
              <br />
               10023
              <br />
              <br />
              1()
              <br />  ()( ) 
              () (  )   
              (  )     
                   .
              <br />
              <br />
              PC,       
                   
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
            </p>
          </div>
        ) : (
          ""
        )}
        <div className="check_box">
          <img
            src={
              checkList2 ? "/images/box_check.jpg" : "/images/box_unCheck.jpg"
            }
            alt=""
            onClick={() => {
              setCheckList2(!checkList2);
              if (checkList1) {
                setCheckListAll(false);
              }
            }}
          />
          <p>
                <span>()</span>
          </p>
          <div
            className="content_box"
            onClick={() => {
              setOpenContent2(!openContent2);
            }}
          >
            <p></p>
            <p>{openContent2 ? "" : ""}</p>
          </div>
        </div>
        {openContent2 ? (
          <div className="content">
            <p>
              () 
              <br />
               10023
              <br />
              <br />
              1()
              <br />  ()( ) 
              () (  )   
              (  )     
                   .
              <br />
              <br />
              PC,       
                   
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
              <br />
              <br />
              2()
              <br />
                ()   (  )
                   
                     , 
                  .
            </p>
          </div>
        ) : (
          ""
        )}
        <div className="check_box">
          <img
            src={
              checkList3 ? "/images/box_check.jpg" : "/images/box_unCheck.jpg"
            }
            alt=""
            onClick={() => {
              setCheckList3(!checkList3);
              if (checkList1) {
                setCheckListAll(false);
              }
            }}
          />
          <p>SNS   ()</p>
        </div>
        <div className="check_box">
          <img
            src={
              checkList4 ? "/images/box_check.jpg" : "/images/box_unCheck.jpg"
            }
            alt=""
            onClick={() => {
              setCheckList4(!checkList4);
              if (checkList1) {
                setCheckListAll(false);
              }
            }}
          />
          <p>   ()</p>
        </div>
      </div>
      <button className="submit_btn" onClick={onSubmitForm}>
        
      </button>
      <button
        className="back_btn"
        onClick={() => {
          navigate("/");
        }}
      >
        
      </button>
    </div>
  );
};

export default Signin;
