import "../../css/order.css";
import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
const Order = () => {
  const location = useLocation();
  const { state } = location;
  const [userName, setUserName] = useState("");
  const [userTel, setUserTel] = useState("");
  const [userEmail, setUserEmail] = useState("");
  const [receiverName, setReceiverName] = useState("");
  const [receiverTel, setReceiverTel] = useState("");
  const [receiverAddress, setReceiverAddress] = useState("");
  const [receiverDetailAddress, setReceiverDetailAddress] = useState("");
  const [requirement, setRequirement] = useState("");
  const [deliveryDate, setDeliveryData] = useState("");
  const [deliveryHour, setDeliveryHour] = useState("");
  const [deliveryMinute, setDeliveryMinute] = useState("");
  const [event, setEvent] = useState("");
  const [eventHour, setEventHour] = useState("");
  const [eventMinute, setEventMinute] = useState("");
  const [eventName, setEventName] = useState("");
  const [ribbonLeft, setRibbonLeft] = useState("");
  const [ribbonRight, setRibbonRight] = useState("");
  const [cardText, setCardText] = useState("");
  const [checkEvent, setCheckEvent] = useState(0);
  const [checkText, setCheckText] = useState(0);
  const [payOption, setPayOption] = useState("");
  const [agree, setAgree] = useState(false);
  const [totalPrice, setTotalPrice] = useState(0);

  const handleUserName = (e) => {
    setUserName(e.target.value);
  };
  const handleUserTel = (e) => {
    setUserTel(e.target.value);
  };
  const handleUserEmail = (e) => {
    setUserEmail(e.target.value);
  };
  const handleReceiverName = (e) => {
    setReceiverName(e.target.value);
  };
  const handleReceiverTel = (e) => {
    setReceiverTel(e.target.value);
  };
  const handleReceiverAddress = (e) => {
    setReceiverAddress(e.target.value);
  };
  const handleReceiverDetailAddress = (e) => {
    setReceiverDetailAddress(e.target.value);
  };
  const handleRequirement = (e) => {
    setRequirement(e.target.value);
  };
  const handleDeliveryDate = (e) => {
    setDeliveryData(e.target.value);
  };
  const handleDeliveryHour = (e) => {
    setDeliveryHour(e.target.value);
  };
  const handleDeliveryMinute = (e) => {
    setDeliveryMinute(e.target.value);
  };
  const handleEvent = (e) => {
    setEvent(e.target.value);
  };
  const handleEventHour = (e) => {
    setEventHour(e.target.value);
  };
  const handleEventMinute = (e) => {
    setEventMinute(e.target.value);
  };
  const handleEventName = (e) => {
    setEventName(e.target.value);
  };
  const handleRibbonLeft = (e) => {
    setRibbonLeft(e.target.value);
  };
  const handleRibbonRight = (e) => {
    setRibbonRight(e.target.value);
  };
  const handleCardText = (e) => {
    setCardText(e.target.value);
  };
  useEffect(() => {
    if (state && state.length > 0) {
      const totalPrice =
        state &&
        state.reduce((acc, product) => {
          return acc + Number(product.product_truePrice);
        }, 0);
      setTotalPrice(totalPrice);
    }
  }, [state]);
  return (
    <div className="order">
      <p></p>
      <div className="article_container">
        <div className="order_container">
          {state &&
            state.map((product, index) => {
              return (
                <div className="product_box" key={index}>
                  <img src={product.product_mainImgSrc} alt="" />
                  <p>{product.product_name}</p>
                  <p>{Number(product.product_truePrice).toLocaleString()}</p>
                </div>
              );
            })}
          <div className="order_box">
            <div className="left_box">
              <p className="title"> </p>
              <input
                type="text"
                value={userName}
                onChange={handleUserName}
                placeholder=""
              />
              <input
                type="text"
                value={userTel}
                onChange={handleUserTel}
                placeholder=""
              />
              <input
                type="text"
                value={userEmail}
                onChange={handleUserEmail}
                placeholder=""
              />
              <p className="title" style={{ marginTop: "2vw" }}>
                 
              </p>
              <input
                type="text"
                value={receiverName}
                onChange={handleReceiverName}
                placeholder=""
              />
              <input
                type="text"
                value={receiverTel}
                onChange={handleReceiverTel}
                placeholder=""
              />
              <div className="address_box">
                <input
                  type="text"
                  value={receiverAddress}
                  onChange={handleReceiverAddress}
                  placeholder=""
                  readOnly
                />
                <div className="search_box"></div>
              </div>
              <input
                type="text"
                value={receiverDetailAddress}
                onChange={handleReceiverDetailAddress}
                placeholder=""
              />
              <p className="detail_text">
                     .
                <br />
                (     .)
              </p>
              <p className="title" style={{ marginTop: "2vw" }}>
                  
              </p>
              <textarea
                type="text"
                value={requirement}
                onChange={handleRequirement}
                placeholder=""
              />
              <p className="add_text">
                      ,  
                 .
              </p>
            </div>
            <div className="right_box">
              <p className="title">  </p>
              <div className="select_box">
                <input
                  type="text"
                  value={deliveryDate}
                  onChange={handleDeliveryDate}
                  placeholder="2024-02-23"
                />
                <select value={deliveryHour} onChange={handleDeliveryHour}>
                  <option value=""></option>
                  <option value="10">10</option>
                  <option value="11">11</option>
                  <option value="12">12</option>
                  <option value="13">13</option>
                  <option value="14">14</option>
                  <option value="15">15</option>
                  <option value="16">16</option>
                  <option value="17">17</option>
                  <option value="18">18</option>
                  <option value="19">19</option>
                  <option value="20">20</option>
                  <option value="21">21</option>
                  <option value="22">22</option>
                  <option value="23">23</option>
                </select>
                <select value={deliveryMinute} onChange={handleDeliveryMinute}>
                  <option value=""></option>
                  <option value="00">00</option>
                  <option value="10">10</option>
                  <option value="20">20</option>
                  <option value="30">30</option>
                  <option value="40">40</option>
                  <option value="50">50</option>
                </select>
                <select value={event} onChange={handleEvent}>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <p className="select_text">
                () /    ,
                .
                <br />
                <br /> ()       3 
                  .(  1566-5565  
                .)
                <br />
                <br />   09:00  22:00.
                <br />
                <br /> 11      
                   !
              </p>
              <p className="title" style={{ marginTop: "2vw" }}>
                 
              </p>
              <div className="event_radio_box">
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckEvent(0);
                  }}
                >
                  <img
                    src={
                      checkEvent === 0
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckEvent(1);
                  }}
                >
                  <img
                    src={
                      checkEvent === 1
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckEvent(2);
                  }}
                >
                  <img
                    src={
                      checkEvent === 2
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
              </div>
              {checkEvent === 0 ? (
                ""
              ) : (
                <>
                  <p className="eventTime"></p>
                  <div className="event_box">
                    <select value={eventHour} onChange={handleEventHour}>
                      <option value=""></option>
                      <option value="10">10</option>
                      <option value="11">11</option>
                      <option value="12">12</option>
                      <option value="13">13</option>
                      <option value="14">14</option>
                      <option value="15">15</option>
                      <option value="16">16</option>
                      <option value="17">17</option>
                      <option value="18">18</option>
                      <option value="19">19</option>
                      <option value="20">20</option>
                      <option value="21">21</option>
                      <option value="22">22</option>
                      <option value="23">23</option>
                    </select>
                    <select value={eventMinute} onChange={handleEventMinute}>
                      <option value=""></option>
                      <option value="00">00</option>
                      <option value="10">10</option>
                      <option value="20">20</option>
                      <option value="30">30</option>
                      <option value="40">40</option>
                      <option value="50">50</option>
                    </select>
                  </div>
                  <input
                    type="text"
                    value={eventName}
                    onChange={handleEventName}
                    placeholder=", ,  "
                  />
                  <p className="input_text">
                     OOO,  OOO,  OOO .
                  </p>
                </>
              )}

              <p className="title" style={{ marginTop: "2vw" }}>
                /
              </p>
              <div className="event_radio_box">
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckText(0);
                  }}
                >
                  <img
                    src={
                      checkText === 0
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckText(1);
                  }}
                >
                  <img
                    src={
                      checkText === 1
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
                <div
                  className="radio_box"
                  onClick={() => {
                    setCheckText(2);
                  }}
                >
                  <img
                    src={
                      checkText === 2
                        ? "/images/radio_on.png"
                        : "/images/radio_off.png"
                    }
                    alt=""
                  />
                  <p></p>
                </div>
              </div>
              {checkText === 0 ? (
                <>
                  {" "}
                  <input
                    type="text"
                    value={ribbonLeft}
                    onChange={handleRibbonLeft}
                    placeholder="  ()"
                  />
                  <input
                    type="text"
                    value={ribbonRight}
                    onChange={handleRibbonRight}
                    placeholder=" ()"
                  />
                </>
              ) : checkText === 1 ? (
                <textarea
                  type="text"
                  value={cardText}
                  onChange={handleCardText}
                  placeholder=""
                />
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
        <div className="pay_container">
          <div className="login_box">
            <p>      .</p>
            <p>GO </p>
          </div>
          <div className="pay_box">
            <p className="title" style={{ marginTop: "2vw" }}>
               
            </p>
            <div className="payOption_box">
              <div
                className="payOption"
                onClick={() => {
                  setPayOption("");
                }}
                style={{
                  borderColor: payOption === "" ? "#2b746a" : "",
                }}
              >
                <img
                  src={
                    payOption === ""
                      ? "/images/btn_check.png"
                      : "/images/btn_unCheck.png"
                  }
                  alt=""
                />
                <p></p>
              </div>
              <div
                className="payOption"
                onClick={() => {
                  setPayOption("");
                }}
                style={{
                  borderColor: payOption === "" ? "#2b746a" : "",
                }}
              >
                <img
                  src={
                    payOption === ""
                      ? "/images/btn_check.png"
                      : "/images/btn_unCheck.png"
                  }
                  alt=""
                />
                <p></p>
              </div>
              <div
                className="payOption"
                onClick={() => {
                  setPayOption("");
                }}
                style={{
                  borderColor: payOption === "" ? "#2b746a" : "",
                }}
              >
                <img
                  src={
                    payOption === ""
                      ? "/images/btn_check.png"
                      : "/images/btn_unCheck.png"
                  }
                  alt=""
                />
                <p></p>
              </div>
              <div
                className="payOption"
                onClick={() => {
                  setPayOption("");
                }}
                style={{
                  borderColor: payOption === "" ? "#2b746a" : "",
                }}
              >
                <img
                  src={
                    payOption === ""
                      ? "/images/btn_check.png"
                      : "/images/btn_unCheck.png"
                  }
                  alt=""
                />
                <p></p>
              </div>
            </div>
            <p className="title" style={{ marginTop: "2vw" }}>
               
            </p>
            <div className="price_box">
              <div className="price_title">
                <p></p>
                <p></p>
                <p></p>
              </div>
              <div className="price">
                <p>{totalPrice.toLocaleString()}</p>
                <p>0</p>
                <p>-{(totalPrice * 0.1).toLocaleString()}</p>
              </div>
            </div>
            <div className="total_box">
              <p> </p>
              <p>{totalPrice.toLocaleString()}</p>
            </div>
            <div
              className="argree_box"
              onClick={() => {
                setAgree(!agree);
              }}
            >
              <img
                src={
                  agree
                    ? "/images/btn_checked.png"
                    : "/images/btn_unChecked.png"
                }
                alt=""
              />
              <p>   .</p>
            </div>
            <p className="info_text">    </p>
            <div className="agree_text">
              <p>
                  
                <br />
                 , ,      
                  .
                <br />
                <br />
                 : , , ID, ,  ,
                 , , , , , ,
                ,  ,  ,  , ,  IP
                , 
                <br />
                <br />
                  : (, )
                <br />
                <br />
                   
                <br />
                      .
              </p>
            </div>
            <div className="pay_btn"></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Order;
